import store from "../redux/store";
import Web3 from "web3";
import unipair_abi from "../abis/IUniswapV2Pair.json";
import {makeBatchRequest} from "./promisify";
import {updatePrices, updateTotals} from "../redux/slices/coineusSlice";

export default function getPairPrices() {
    const _store = store.getState();
    const {pairs, rpc} = _store.coineus;

    getFUSE(pairs, rpc.fuse);
    getAVAX(pairs, rpc.avax);
    getBNB(pairs, rpc.bnb);
    getETH(pairs, rpc.eth);
}

function getFUSE(pairs, rpc) {

    //FUSE
    const web3 = new Web3(rpc);
    const fusd_fuse = new web3.eth.Contract(unipair_abi, pairs.fUSDFUSEpair);
    const cns_fuse = new web3.eth.Contract(unipair_abi, pairs.CNSFUSEpair);
    const volt_fuse = new web3.eth.Contract(unipair_abi, pairs.VOLTFUSEpair);

    const calls = [
        fusd_fuse.methods.getReserves().call,
        cns_fuse.methods.getReserves().call,
        volt_fuse.methods.getReserves().call
    ]

    makeBatchRequest(web3, calls)
        .then(resp => {
            const [
                fusd_fuse_reserves,
                cns_fuse_reserves,
                volt_fuse_reserves
            ] = resp

            //FUSE
            store.dispatch(updatePrices({
                chain: 'fuse',
                contract_address: 'native',
                price: {
                    inUSD: fusd_fuse_reserves.reserve1/fusd_fuse_reserves.reserve0,
                    inFuse: 1
                }
            }));

            //FUSD
            store.dispatch(updatePrices({
                chain: 'fuse',
                contract_address: '0x249BE57637D8B013Ad64785404b24aeBaE9B098B',
                price: {
                    inUSD: 1,
                    inFuse: fusd_fuse_reserves.reserve0/fusd_fuse_reserves.reserve1
                }
            }));

            //CNS
            store.dispatch(updatePrices({
                chain: 'fuse',
                contract_address: '0xc2E299b47398963c618De5b05C6BdeCd4cc64022',
                price: {
                    inFuse: cns_fuse_reserves.reserve0/cns_fuse_reserves.reserve1,
                    inUSD: cns_fuse_reserves.reserve0/cns_fuse_reserves.reserve1 * fusd_fuse_reserves.reserve1/fusd_fuse_reserves.reserve0
                }
            }));

            //CNSONE
            store.dispatch(updatePrices({
                chain: 'fuse',
                contract_address: '0x7042807d547A2de0720421a6d2f629c9EB43E73D',
                price: {
                    inFuse: cns_fuse_reserves.reserve0/cns_fuse_reserves.reserve1,
                    inUSD: cns_fuse_reserves.reserve0/cns_fuse_reserves.reserve1 * fusd_fuse_reserves.reserve1/fusd_fuse_reserves.reserve0
                }
            }));

            //VOLT
            store.dispatch(updatePrices({
                chain: 'fuse',
                contract_address: '0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4',
                price: {
                    inFuse: volt_fuse_reserves.reserve0/volt_fuse_reserves.reserve1,
                    inUSD: volt_fuse_reserves.reserve0/volt_fuse_reserves.reserve1 * fusd_fuse_reserves.reserve1/fusd_fuse_reserves.reserve0
                }
            }));

            store.dispatch(updateTotals('fuse'));
        });
}

function getAVAX(pairs, rpc) {


    //AVAX
    const web3AVAX = new Web3(rpc);
    const usdc_avax = new web3AVAX.eth.Contract(unipair_abi, pairs.USDCAVAXpair);
    const fook_avax = new web3AVAX.eth.Contract(unipair_abi, pairs.FOOKAVAXpair);
    const fud_avax = new web3AVAX.eth.Contract(unipair_abi, pairs.FUDAVAXpair);
    const hedge_avax = new web3AVAX.eth.Contract(unipair_abi, pairs.HEDGEAVAXpair);
    const burnit_avax = new web3AVAX.eth.Contract(unipair_abi, pairs.BRNAVAXpair);

    const callsAVAX = [
        usdc_avax.methods.getReserves().call,
        fook_avax.methods.getReserves().call,
        fud_avax.methods.getReserves().call,
        hedge_avax.methods.getReserves().call,
        burnit_avax.methods.getReserves().call
    ]

    makeBatchRequest(web3AVAX, callsAVAX)
        .then(resp => {
            const [
                usdc_avax_reserves,
                fook_avax_reserves,
                fud_avax_reserves,
                hedge_avax_reserves,
                burnit_avax_reserves
            ] = resp

            const avax_price = usdc_avax_reserves.reserve1/usdc_avax_reserves.reserve0 * (10**12)

            //AVAX
            store.dispatch(updatePrices({
                chain: 'avax',
                contract_address: 'native',
                price: {
                    inUSD: avax_price,
                    inAVAX: 1
                }
            }));

            //WAVAX
            store.dispatch(updatePrices({
                chain: 'avax',
                contract_address: '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7',
                price: {
                    inUSD: avax_price,
                    inAVAX: 1
                }
            }));

            //USDC
            store.dispatch(updatePrices({
                chain: 'avax',
                contract_address: '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E',
                price: {
                    inUSD: 1,
                    inAVAX: usdc_avax_reserves.reserve0/usdc_avax_reserves.reserve1 / (10**12)
                }
            }));

            //FOOK
            store.dispatch(updatePrices({
                chain: 'avax',
                contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744',
                price: {
                    inAVAX: fook_avax_reserves.reserve1/fook_avax_reserves.reserve0,
                    inUSD: fook_avax_reserves.reserve1/fook_avax_reserves.reserve0 * avax_price
                }
            }));

            //FUD
            store.dispatch(updatePrices({
                chain: 'avax',
                contract_address: '0x80363cc2CAa2545554d155031e47049ee2F98f33',
                price: {
                    inAVAX: fud_avax_reserves.reserve1/fud_avax_reserves.reserve0,
                    inUSD: fud_avax_reserves.reserve1/fud_avax_reserves.reserve0 * avax_price
                }
            }));

            //HEDGE FUND
            store.dispatch(updatePrices({
                chain: 'avax',
                contract_address: '0xe387cF241B6685D405C575ce35db78ed4844B34a',
                price: {
                    inAVAX: hedge_avax_reserves.reserve0/hedge_avax_reserves.reserve1,
                    inUSD: hedge_avax_reserves.reserve0/hedge_avax_reserves.reserve1 * avax_price
                }
            }));

            const brn0 = burnit_avax_reserves.reserve0 / (10**9)
            const brn1 = burnit_avax_reserves.reserve1 / (10**18)

            //BURNIT
            store.dispatch(updatePrices({
                chain: 'avax',
                contract_address: '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00',
                price: {
                    inAVAX: brn1/brn0,
                    inUSD: brn1/brn0 * avax_price
                }
            }));

            store.dispatch(updateTotals('avax'));

        });
}

function getBNB(pairs, rpc) {


    //BSC
    const web3BSC = new Web3(rpc);
    const bnb_busd = new web3BSC.eth.Contract(unipair_abi, pairs.BNBBUSDpair);
    const feg_bnb = new web3BSC.eth.Contract(unipair_abi, pairs.FEGBNBpair);

    const callsBNB = [
        bnb_busd.methods.getReserves().call,
        feg_bnb.methods.getReserves().call
    ]

    makeBatchRequest(web3BSC, callsBNB)
        .then(resp => {
            const [
                bnb_busd_reserves,
                feg_bnb_reserves
            ] = resp

            const bnb_price = bnb_busd_reserves.reserve0/bnb_busd_reserves.reserve1

            //BNB
            store.dispatch(updatePrices({
                chain: 'bnb',
                contract_address: 'native',
                price: {
                    inUSD: bnb_price,
                    inBNB: 1
                }
            }));

            //FEG BSC
            store.dispatch(updatePrices({
                chain: 'bnb',
                contract_address: '0xacFC95585D80Ab62f67A14C566C1b7a49Fe91167',
                price: {
                    inUSD: feg_bnb_reserves.reserve1/feg_bnb_reserves.reserve0/(10**9) * bnb_price ,
                    inBNB: feg_bnb_reserves.reserve1/feg_bnb_reserves.reserve0/(10**9)
                }
            }));

            store.dispatch(updateTotals('bnb'));

        });

}

function getETH(pairs, rpc) {


    //ETH
    const web3ETH = new Web3(rpc);
    const eth_usdc = new web3ETH.eth.Contract(unipair_abi, pairs.ETHUSDCPair);
    const okinami_usdc = new web3ETH.eth.Contract(unipair_abi, pairs.OKINAMIUSDCPair);
    const feg_eth = new web3ETH.eth.Contract(unipair_abi, pairs.FEGETHpair);


    const callsETH = [
        eth_usdc.methods.getReserves().call,
        okinami_usdc.methods.getReserves().call,
        feg_eth.methods.getReserves().call
    ]

    makeBatchRequest(web3ETH, callsETH)
        .then(resp => {

            const [
                eth_usdc_reserves,
                okinami_usdc_reserves,
                feg_eth_reserves
            ] = resp

            const eth_price = eth_usdc_reserves.reserve0/eth_usdc_reserves.reserve1*(10**12)

            //ETH
            store.dispatch(updatePrices({
                chain: 'eth',
                contract_address: 'native',
                price: {
                    inUSD: eth_price,
                    inETH: 1
                }
            }));

            const oki0 = okinami_usdc_reserves.reserve0 / (10**9)
            const oki1 = okinami_usdc_reserves.reserve1 / (10**6)

            //OKINAMI
            store.dispatch(updatePrices({
                chain: 'eth',
                contract_address: '0x1C4853Ec0d55e420002c5eFaBc7Ed8e0bA7A4121',
                price: {
                    inUSD: oki1/oki0,
                    inETH: oki1/oki0
                }
            }));

            //FEG ETH
            store.dispatch(updatePrices({
                chain: 'eth',
                contract_address: '0x389999216860AB8E0175387A0c90E5c52522C945',
                price: {
                    inUSD: feg_eth_reserves.reserve1/feg_eth_reserves.reserve0 / (10**9) * eth_price,
                    inETH: feg_eth_reserves.reserve1/feg_eth_reserves.reserve0 / (10**9)
                }
            }));

            store.dispatch(updateTotals('eth'));

        })
        .catch(console.log);

}

