import * as React from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";
import {Paper, Typography, Grid} from "@mui/material";
import {makeStyles} from "@mui/styles";
import {Autorenew, Whatshot, Savings} from "@mui/icons-material";

const useStyles = makeStyles((theme) => ({
    xyplot: {
        margin: '0 auto',
    },
}));

function BurnSupply(props) {
    const classes = useStyles();

    const {burnt, supply, percent, staked} = props;

    const angle = 360 * percent * (Math.PI / 180);

    const percent2 = staked ? (staked - burnt) / supply : 0;
    const angle2 = 360 * percent2 * (Math.PI / 180);

    const burntValue = (percent * 100).toFixed(2);
    const percentStaked = staked ? ((staked / supply) * 100).toFixed(2) : 0;

    const colors = {
        supply: '#75de00',
        burned: '#FF5733',
        staked: '#C70039',
    }

    return (
        <Paper style={{padding: 20}}>
            <FlexibleWidthXYPlot
                xDomain={[-5, 5]}
                yDomain={[-5, 5]}
                height={250}
                margin={{left: 0, right: 0, top: 0, bottom: 0}}
                className={classes.xyplot}
            >
                <ArcSeries
                    radiusDomain={[0, 10]}
                    center={{x: 0, y: 0}}
                    data={[
                        {
                            angle0: 0,
                            angle: Math.PI * 2,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.supply,
                        },
                        {
                            angle0: angle,
                            angle: angle + angle2,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.staked,
                        },
                        {
                            angle0: 0,
                            angle: angle,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.burned,
                        },
                    ]}
                    colorType={'literal'}
                />
            </FlexibleWidthXYPlot>
            <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -155,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700',
                }}
            >
                {burntValue}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % BURNED
        </span>
            </Typography>
            <Grid container direction="row" alignItems="center">
                <Grid item>
                    <Autorenew
                        style={{color: colors.supply, marginRight: 10, marginTop: 6}}
                    />
                </Grid>
                <Grid item>
                    <Typography style={{color: colors.supply, fontSize: 18}}>{supply.toLocaleString()} <Typography
                        variant="caption">SUPPLY</Typography></Typography>
                </Grid>
            </Grid>

            <Grid container direction="row" alignItems="center">
                <Grid item>
                    <Whatshot
                        style={{color: colors.burned, marginRight: 10, marginTop: 3}}
                    />
                </Grid>
                <Grid item>
                    <Typography style={{color: colors.burned, fontSize: 18}}>{burnt.toLocaleString()} <Typography
                        variant="caption">BURNED</Typography></Typography>
                </Grid>
            </Grid>
        </Paper>
    );
}

export default BurnSupply;
