import {useEffect, useState} from "react";
import Web3 from "web3";
import coineus_abi from "../abis/coineus.json";
import {makeBatchRequest} from "../utils/promisify";
import {Container, Grid, ListItem, ListItemText, Paper} from "@mui/material";
import BurnSupply from "../components/BurntSupply";
import {useGetTokenQuery} from "../redux/services/dexscreener";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {useSelector} from "react-redux";

const FUD_TOKEN_ADDRESS = '0x80363cc2CAa2545554d155031e47049ee2F98f33';

export default function Stats() {

    const {rpc} = useSelector( state => state.coineus);
    const [totalD, setTotalD] = useState(0);

    const [totalStaked, setTotalStaked] = useState(0);
    const [totalBurnt, setTotalBurnt] = useState(0);
    const [validatorBal, setValidatorBal] = useState(0);

    const {data} = useGetTokenQuery(FUD_TOKEN_ADDRESS, {
        pollingInterval: 60000
    });

    //updates coineus info
    useEffect(() => {
        getCoineusInfo();
        const interval = setInterval(getCoineusInfo, 60000)
        return () => clearInterval(interval);
    }, [])

    const getCoineusInfo = () => {
        const web3 = new Web3(rpc.avax);
        const contract = new web3.eth.Contract(coineus_abi, FUD_TOKEN_ADDRESS);

        const calls = [
            contract.methods.getTotalDividendsDistributed().call,
            contract.methods.balanceOf('0x000000000000000000000000000000000000dEaD').call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                td,
                burnt
            ] = resp;

            setTotalD(td / (10 ** 18));
            setTotalBurnt(burnt / (10 ** 18))
        });

    }

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                <Grid item xs={12}>
                    <Paper style={{color: '#000', backgroundColor: '#75de00'}}>
                        <ListItem>
                            <ListItemText
                                primary={`${totalD.toLocaleString('en-US', {
                                    maximumFractionDigits: 2,
                                    minimumFractionDigits: 2
                                })} WAVAX` || '--'}
                                secondary="Total Rewards All-time"
                                primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                                secondaryTypographyProps={{color: '#000'}}
                            />
                        </ListItem>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <BurnSupply
                        supply={100000000}
                        burnt={totalBurnt}
                        staked={0}
                        percent={totalBurnt / 100000000}
                    />
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(data?.pairs[0]?.fdv)}
                                secondary="Fully Diluted Value"
                                primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                            />
                        </ListItem>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(data?.pairs[0]?.liquidity.usd)}
                                secondary="Total Liquidity"
                                primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(data?.pairs[0]?.liquidity.base)}
                                secondary="FUD Token (FUD)"
                                primaryTypographyProps={{ fontWeight: 700}}
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(data?.pairs[0]?.liquidity.quote)}
                                secondary="Wrapped AVAX (WAVAX)"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(data?.pairs[0]?.volume.h24)}
                                secondary="24 Hour Volume"
                                primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={data?.pairs[0]?.txns.h24.buys}
                                secondary="Buys"
                                primaryTypographyProps={{ fontWeight: 700}}
                            />
                            <ListItemText
                                primary={data?.pairs[0]?.txns.h24.sells}
                                secondary="Sells"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                    </Paper>
                </Grid>

            </Grid>
        </Container>
    )

}