import {useSelector} from "react-redux";
import {
    Button,
    Container,
    Grid,
    ListItem,
    IconButton,
} from "@mui/material";

import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';
import wavax_abi from "../abis/wavax_abi.json";

import {useState} from "react";
import ChangeCircleIcon from '@mui/icons-material/ChangeCircle';

import {toWei} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";
import TokenListItem from "../components/TokenListItem";
import getUserBalances from "../utils/get_balances";

export default function HomePage() {

    const {wallet} = useSelector(state => state.coineus);

    const [pending, setPending] = useState(false);

    const claimFud = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineus_abi, '0x80363cc2CAa2545554d155031e47049ee2F98f33');

            contract.methods
                .claim()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getUserBalances(wallet.address);
                    setPending(false);
                });
        }
    }

    const unwrapAVAX = async (amount) => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(wavax_abi, "0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7");


            contract
                .methods
                .allowance(wallet.address, "0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7")
                .call((err, resp) => {

                    if (err) return;

                    if (resp < amount) {
                        contract
                            .methods
                            .approve("0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7", toWei(amount * 100))
                            .send({from: wallet.address})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                withdraw()
                            });
                    } else {
                        withdraw()
                    }
                })

            const withdraw = () => {
                const amt = toWei(amount - .0000000000000001);
                contract.methods.withdraw(amt)
                    .send({from: wallet.address})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        getUserBalances(wallet.address);
                        setPending(false);
                    });
            }
        }
    }

    return (<Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                <Grid item xs={12}>

                    <TokenListItem
                        contract_address="native"
                        network="avax"
                        name="AVAX"
                        native
                        price={wallet.prices.avax?.native}
                        wrap="0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7"
                    />

                    {/*WAVAX*/}
                    <TokenListItem
                        contract_address="0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7"
                        network="avax"
                        name="WAVAX"
                        price={wallet.prices.avax?.["0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7"]}
                        secondaryAction={
                            <IconButton
                                disabled={
                                    wallet.chain !== 43114 ||
                                    wallet.balances.avax["0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7"] < 1 / (10 ** 16)
                                }
                                edge="end"
                                onClick={() => unwrapAVAX(wallet.balances.avax["0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7"])}
                                color="primary"
                            >
                                <ChangeCircleIcon/>
                            </IconButton>
                        }
                    />

                    {/*FUD*/}
                    <TokenListItem
                        contract_address="0x80363cc2CAa2545554d155031e47049ee2F98f33"
                        network="avax"
                        name="FUD"
                        price={wallet.prices.avax?.["0x80363cc2CAa2545554d155031e47049ee2F98f33"]}
                        rewards={{
                            allTime: wallet.info.avax?.["0x80363cc2CAa2545554d155031e47049ee2F98f33"]?.totalDividends,
                            pending: wallet.info.avax?.["0x80363cc2CAa2545554d155031e47049ee2F98f33"]?.withdrawableDividends,
                            symbol: 'WAVAX'
                        }}
                        rewardsClaim={
                            <ListItem style={{color: '#75de00'}}>
                                <Button onClick={claimFud} variant='contained' fullWidth
                                        disabled={
                                            wallet.chain !== 43114 ||
                                            wallet.info.avax?.["0x80363cc2CAa2545554d155031e47049ee2F98f33"]?.withdrawableDividends === 0 ||
                                            !wallet.address ||
                                            wallet.info.avax?.["0x80363cc2CAa2545554d155031e47049ee2F98f33"]?.secondsUntilAutoClaimAvailable > 0
                                        }>{wallet.chain !== 43114 ? 'Change to AVAX Chain' : `Claim ${wallet.info.avax?.["0x80363cc2CAa2545554d155031e47049ee2F98f33"]?.secondsUntilAutoClaimAvailable > 0 ? 'Wait' : ''}`}</Button>
                            </ListItem>
                        }
                    />
                </Grid>
            </Grid>

            <CoineusLoading open={pending} label="Transaction Pending"/>

        </Container>
    );
}