import store from "../redux/store";
import Web3 from "web3";
import {makeBatchRequest} from "./promisify";
import {updateBalances, updateInfo, updateTotals} from "../redux/slices/coineusSlice";
import coineus_abi from "../abis/coineus.json";
import coineusone_abi from "../abis/coineusone_abi.json";

export default function getUserBalances(wallet_address) {
    const _store = store.getState();
    const {pairs, rpc} = _store.coineus;

    if(wallet_address === undefined){
        return;
    }

    getFUSE(pairs, rpc.fuse, wallet_address);
    getAVAX(pairs, rpc.avax, wallet_address);
    getBNB(pairs, rpc.bnb, wallet_address);
    getETH(pairs, rpc.eth, wallet_address);
}

function getFUSE(pairs, rpc, wallet_address) {

    const web3 = new Web3(rpc);
    const contract = new web3.eth.Contract(coineus_abi, "0xc2E299b47398963c618De5b05C6BdeCd4cc64022");
    const cnsone_contract = new web3.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");
    const fusd_contract = new web3.eth.Contract(coineus_abi, '0x249BE57637D8B013Ad64785404b24aeBaE9B098B');
    const volt_contract = new web3.eth.Contract(coineus_abi, '0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4');

    const calls = [
        web3.eth.getBalance(wallet_address),
        contract.methods.balanceOf(wallet_address).call,
        contract.methods.getAccountDividendsInfo(wallet_address).call,
        cnsone_contract.methods.balanceOf(wallet_address).call,
        cnsone_contract.methods.getAccount(wallet_address).call,
        fusd_contract.methods.balanceOf(wallet_address).call,
        volt_contract.methods.balanceOf(wallet_address).call
    ];

    makeBatchRequest(web3, calls).then(resp => {
        let [
            fuse_balance,
            cns_balance,
            info,
            cnsone_balance,
            cnsone_info,
            fusd_balance,
            volt_balance
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: 'native',
            balance: fuse_balance / (10 ** 18)
        }))

        let withdrawableDividends = info[3];
        let totalDividends = info[4];
        let secondsUntilAutoClaimAvailable = info[7];

        cns_balance = cns_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 18);
        totalDividends = totalDividends / (10 ** 18);

        store.dispatch(updateInfo({
            chain: 'fuse',
            contract_address: "0xc2E299b47398963c618De5b05C6BdeCd4cc64022",
            info: {
                cns_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        //coineus one
        const {account, balance, deposits, fees, transfersIn, transfersOut, withdrawals} = cnsone_info;
        store.dispatch(updateInfo({
            chain: 'fuse',
            contract_address: "0x7042807d547A2de0720421a6d2f629c9EB43E73D",
            info: {account, balance, deposits, fees, transfersIn, transfersOut, withdrawals}
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: "0xc2E299b47398963c618De5b05C6BdeCd4cc64022",
            balance: cns_balance
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x249BE57637D8B013Ad64785404b24aeBaE9B098B',//fUSD
            balance: fusd_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4',//fUSD
            balance: volt_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: "0x7042807d547A2de0720421a6d2f629c9EB43E73D",//CNSONE
            balance: cnsone_balance / (10 ** 18)
        }));

        store.dispatch(updateTotals('fuse'));
    })
}

function getAVAX(pairs, rpc, wallet_address) {

    const web3AVAX = new Web3(rpc);
    const fook_contract = new web3AVAX.eth.Contract(coineus_abi, '0x95923F63dA09B4f7520f7C65a31F318D8228B744');
    const fud_contract = new web3AVAX.eth.Contract(coineus_abi, '0x80363cc2CAa2545554d155031e47049ee2F98f33');
    const usdc_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E');
    const wavax_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7');
    const hedge_contract = new web3AVAX.eth.Contract(coineus_abi, '0xe387cF241B6685D405C575ce35db78ed4844B34a');
    const burnit_contract = new web3AVAX.eth.Contract(coineus_abi, '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00');

    const callsAVAX = [
        web3AVAX.eth.getBalance(wallet_address),
        fook_contract.methods.balanceOf(wallet_address).call,
        fook_contract.methods.getAccountDividendsInfo(wallet_address).call,
        usdc_contract.methods.balanceOf(wallet_address).call,
        fud_contract.methods.balanceOf(wallet_address).call,
        fud_contract.methods.getAccountDividendsInfo(wallet_address).call,
        wavax_contract.methods.balanceOf(wallet_address).call,
        hedge_contract.methods.balanceOf(wallet_address).call,
        hedge_contract.methods.getAccountDividendsInfo(wallet_address).call,
        burnit_contract.methods.balanceOf(wallet_address).call,
    ];

    makeBatchRequest(web3AVAX, callsAVAX).then(resp => {
        let [
            avax_balance,
            balance,
            info,
            usdc_balance,
            fud_balance,
            fud_info,
            wavax_balance,
            hedge_balance,
            hedge_info,
            burnit_balance
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: 'native',
            balance: avax_balance / (10 ** 18)
        }))

        let withdrawableDividends = info[3];
        let totalDividends = info[4];
        let secondsUntilAutoClaimAvailable = info[7];

        balance = balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 6);
        totalDividends = totalDividends / (10 ** 6);

        store.dispatch(updateInfo({
            chain: 'avax',
            contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744',
            info: {
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744', //fook
            balance: balance
        }))

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E', //usdc
            balance: usdc_balance / (10 ** 6)
        }))

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7', //wavax
            balance: wavax_balance / (10 ** 18)
        }))

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00', //burnit
            balance: burnit_balance / (10 ** 9)
        }))

        // FUD TOKEN
        withdrawableDividends = fud_info[3];
        totalDividends = fud_info[4];
        secondsUntilAutoClaimAvailable = fud_info[7];

        fud_balance = fud_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 18);
        totalDividends = totalDividends / (10 ** 18);

        store.dispatch(updateInfo({
            chain: 'avax',
            contract_address: '0x80363cc2CAa2545554d155031e47049ee2F98f33',
            info: {
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0x80363cc2CAa2545554d155031e47049ee2F98f33', //fud
            balance: fud_balance
        }))

        // HEDGE TOKEN
        withdrawableDividends = hedge_info[3];
        totalDividends = hedge_info[4];
        secondsUntilAutoClaimAvailable = hedge_info[7];

        hedge_balance = hedge_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 18);
        totalDividends = totalDividends / (10 ** 18);

        store.dispatch(updateInfo({
            chain: 'avax',
            contract_address: '0xe387cF241B6685D405C575ce35db78ed4844B34a',
            info: {
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0xe387cF241B6685D405C575ce35db78ed4844B34a', //hedge
            balance: hedge_balance
        }))

        store.dispatch(updateTotals('avax'));

    })
        .catch(console.log)
}

function getBNB(pairs, rpc, wallet_address) {
    const web3BSC = new Web3(rpc);
    const feg_contract = new web3BSC.eth.Contract(coineus_abi, '0xacFC95585D80Ab62f67A14C566C1b7a49Fe91167');

    const callsBSC = [
        web3BSC.eth.getBalance(wallet_address),
        feg_contract.methods.balanceOf(wallet_address).call,
    ];

    makeBatchRequest(web3BSC, callsBSC).then(resp => {
        let [
            bnb_balance,
            balance,
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'bnb',
            contract_address: 'native',
            balance: bnb_balance / (10 ** 18)
        }))

        store.dispatch(updateBalances({
            chain: 'bnb',
            contract_address: '0xacFC95585D80Ab62f67A14C566C1b7a49Fe91167',
            balance: balance / (10 ** 9)
        }))

        store.dispatch(updateTotals('bnb'));

    })
}

function getETH(pairs, rpc, wallet_address) {
    const web3ETH = new Web3(rpc);
    const okinami_contract = new web3ETH.eth.Contract(coineus_abi, '0x1C4853Ec0d55e420002c5eFaBc7Ed8e0bA7A4121');
    const feg_eth_contract = new web3ETH.eth.Contract(coineus_abi, '0x389999216860AB8E0175387A0c90E5c52522C945');

    const callsETH = [
        web3ETH.eth.getBalance(wallet_address),
        okinami_contract.methods.balanceOf(wallet_address).call,
        feg_eth_contract.methods.balanceOf(wallet_address).call,
    ];

    makeBatchRequest(web3ETH, callsETH).then(resp => {
        let [
            eth_balance,
            okinami_balance,
            feg_eth_balance,
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: 'native',
            balance: eth_balance / (10 ** 18)
        }))

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: '0x1C4853Ec0d55e420002c5eFaBc7Ed8e0bA7A4121',
            balance: okinami_balance / (10 ** 9)
        }))

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: '0x389999216860AB8E0175387A0c90E5c52522C945',
            balance: feg_eth_balance / (10 ** 9)
        }))

        store.dispatch(updateTotals('eth'));

    })
}